






import { Component, Prop, Vue, Watch } from 'vue-property-decorator';

@Component
export default class QuizesTabNav extends Vue {
  @Prop({type: Number, default: 0}) public readonly defaultSelected!: number;
  private active?: number = -1;
  private tabs?: string[] = [
    '排行榜',
    '最新竞猜',
    '我的竞猜',
    '我的订阅',
  ];
  @Watch('active')
  private activeChange(value: number) {
    this.$emit('tab-change', value);
  }
  private mounted() {
    this.active = this.defaultSelected;
  }
}
