























import { Component, Prop, Vue } from 'vue-property-decorator';
import QuizesGameSelector from './QuizesGameSelector.vue';
import LotteryCard from '@/application/Lottery/View/LotteryCard.vue';
import QuizesTabNav from './QuizesTabNav.vue';
import QuizesActions from './QuizesActions.vue';
import QuizesTopTable from './QuizesTopTable.vue';
import QuizesLatestQuiz from './QuizesLatestQuiz.vue';
import QuizesMyQuiz from './QuizesMyQuiz.vue';
import QuizesMySubscription from './QuizesMySubscription.vue';

@Component({
  components: {
    QuizesGameSelector,
    LotteryCard,
    QuizesTabNav,
    QuizesActions,
    QuizesTopTable,
    QuizesLatestQuiz,
    QuizesMyQuiz,
    QuizesMySubscription
  }
})
export default class QuizesView extends Vue {
  private defaultSelectedTab?: number = 0;
  private active?: number = -1;
  private tabChange(value: number) {
    this.active = value;
  }
}
